// import uniNumberBox from "/components/uni-number-box/uni-number-box.vue
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
export default {
	 // components: {uniNumberBox},
	data() {
		return {
			token: uni.getStorageSync('cdj_token'),
			kind: 0,
			arrowDirect: false,
			ware: [],
			recommend: [],
			recommendLength: '',
			value:1,
			id: '',
			hours: '0',
			minu: 0,
			second: 0,
			attrspec: '',
			spec: '',
			nav: false,
			imgRemote: imgRemote
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			})
		},
		showKey() {
			this.$refs.popup.open();
		},
		selectSpec(index) {
			this.kind = index;
			this.attrspec = this.ware.attr[index];
			console.log(index)
		},
		getItem() {
			let {
				id
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				id: id,
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			//单个商品详情
			rs.getRequests('getItemById', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.info = data.data.info;
					this.ware = data.data;
					if (data.data.attr.length > 0) {
						this.spec = true;
						this.attrspec = data.data.attr[0];
					} else {
						this.spec = false;
					}
					this.collect = data.data.collect_status;
					this.hours = data.data.panicActivity.timeRemain / 3600;
				}
			});
			// 推荐列表
			rs.getRequests('itemRecommend', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.recommend = data.data;
					if (data.data.list.length) {
						this.recommendLength = true;
					}
				} 
			});
		},
		detailPage(id) {
			uni.navigateTo({
				url: 'shopdetail?id=' + id
			});
		},
		addCart(){
	
		},
		toParent(e){
		
			log(this.value)
			this.value=e.val;
		}
		
	},
	onLoad(option) {
		//option为object类型，会序列化上个页面传递的参数
		this.id = option.id;
	},
	onShow() {
		this.getItem();
	},
	onPageScroll(e) {
		if (e.scrollTop == 0) {
			this.nav = false;
		} else {
			this.nav = true;
		}
	}
};
